import Typography from '@mui/material/Typography'
import { useEffect, useState } from 'react';
import { useParams } from 'react-router'
import Grid from '@mui/material/Grid'
import { Divider, Table, TableCell, TableContainer, TableHead, TableRow, TextField } from '@mui/material';
import NotFound from '../../app/errors/NotFound';
import Loader from '../../app/layout/Loader';
import { currencyFormat } from '../../app/helper/helper';
import { LoadingButton } from '@mui/lab';
import { useAppDispatch, useAppSelector } from '../../app/store/configureStore';
import { addBasketItemAsync, removeBasketItemAsync } from '../basket/basketSlice';
import { fetchProductAsync, productSelectors } from './catalogSlice';

export default function ProductDetails() {
    const { basket, status } = useAppSelector(state => state.basket);
    const dispatch = useAppDispatch();
    const { id } = useParams();
    const product = useAppSelector(state => productSelectors.selectById(state, id!));
    const { status: productStatus } = useAppSelector(state => state.catalog);
    const [quantity, setQuantity] = useState(0);
    const item = basket?.items.find(i => i.productId === product?.id);
    useEffect(() => {
        document.body.style.background = 'inherit';
        if (item) setQuantity(item.quantity);
        if (!product) dispatch(fetchProductAsync(id!))
    }, [id, item, product, dispatch])
    useEffect(() => {
        return () => {
            document.body.style.background = '#e7e7e7'
        }
    }, [])

    const handleInputChange = (event: any) => {
        if (event.target.value >= 0)
            setQuantity(event.target.value);
    }

    const handleUpdateCart = () => {
        if (!item || quantity > item.quantity) {
            const updatedQuantity = item ? quantity - item.quantity : quantity;
            dispatch(addBasketItemAsync({ productId: product?.id!, quantity: updatedQuantity }));
        } else {
            const updatedQuantity = item.quantity - quantity;
            dispatch(removeBasketItemAsync({ productId: product?.id!, quantity: updatedQuantity }));
        }
    }

    if (productStatus.includes('pending')) return (
        <Loader message='Loading Product...' />
    )
    if (!product) return (
        <NotFound />
    )
    return (
        <Grid container spacing={6} marginTop={0}>
            <Grid item xs={12} md={6}>
                <img src={product.pictureUrl} alt={product.name} />
            </Grid>
            <Grid item xs={12} md={6}>
                <Typography variant="h4" color="initial">{product.name}</Typography>
                <Divider sx={{ borderBottomWidth: 'medium', my: 2 }} variant="fullWidth" />
                <Typography fontWeight="bolder" color="primary.dark" variant="h4">
                    {currencyFormat(product.price)}
                </Typography>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Name</TableCell>
                                <TableCell>{product.name}</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableHead>
                            <TableRow>
                                <TableCell>Description</TableCell>
                                <TableCell>{product.description}</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableHead>
                            <TableRow>
                                <TableCell>Type</TableCell>
                                <TableCell>{product.type}</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableHead>
                            <TableRow>
                                <TableCell>Brand</TableCell>
                                <TableCell>{product.brand}</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableHead>
                            <TableRow>
                                <TableCell>In Stock</TableCell>
                                <TableCell>{product.quantityInStock}</TableCell>
                            </TableRow>
                        </TableHead>

                    </Table>
                </TableContainer>
                <Grid container spacing={2} marginTop={1}>
                    <Grid item xs={6}>
                        <TextField
                            onChange={handleInputChange}
                            variant='outlined'
                            type='number'
                            label='Quantity in Cart'
                            fullWidth
                            value={quantity}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <LoadingButton
                            disabled={(item?.quantity === quantity) || (!item && quantity === 0)}
                            loading={(status.includes('pending'))}
                            onClick={handleUpdateCart}
                            sx={{ height: '55px' }}
                            color='primary'
                            size='large'
                            variant='contained'
                            fullWidth
                        >
                            {item ? 'Update Quantity' : 'Add to Cart'}
                        </LoadingButton>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    )
}
