import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { Paper } from '@mui/material';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { FieldValues, useForm } from 'react-hook-form';
import { LoadingButton } from '@mui/lab';
import { useAppDispatch, useAppSelector } from '../../app/store/configureStore';
import { SignInUser } from './accountSlice';
import { useEffect } from 'react';

export default function Login() {
  const {user} = useAppSelector(state=>state.account);
 const dispatch = useAppDispatch();
 const location = useLocation();
 const navigate = useNavigate();
  const {register ,handleSubmit,formState:{isSubmitting , errors, isValid}}= useForm({
    mode:'all'
  });
  const submitForm=async (data:FieldValues) => {
    await dispatch(SignInUser(data)).then(data=>{
      if(!(data.meta.requestStatus==='rejected')){
        navigate((location.state as any)?.from?.pathname || '../');
      }
    });
    const fromPathname = (location.state as any)?.from?.pathname || '../';
    navigate(fromPathname);
    
  }
  useEffect(() => {
    const fromPathname = (location.state as any)?.from?.pathname || '../';
    if (user) {
      navigate(fromPathname);
    }
  }, [user, navigate, location.state]);
  
  return (
    
      <Container component={Paper} maxWidth="sm">
       
        <Box
          sx={{
            marginTop: 20,
            p:3,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'primary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <Box component="form" onSubmit={handleSubmit(submitForm)} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              fullWidth
              label="Username"
              autoFocus
              {...register('username',{ required: 'Username is required'})}
              error={!!errors.username}
              helperText={errors?.username?.message as React.ReactNode ?? ""}
                
            />
            <TextField
              margin="normal"
              fullWidth
              label="Password"
              type="password"
              {...register('password',{ required: 'Password is required'})}
              error={!!errors.password}
              helperText={errors?.password?.message as React.ReactNode ?? ""}
            />
          
            <LoadingButton
            disabled={!isValid}
            loading={isSubmitting}
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign In
            </LoadingButton>
            <Grid container>
             
              <Grid item>
                <Typography 
                component={Link}
                 to="../register" 
                 sx={{fontWeight:'bold' , color:'primary.main' , textDecoration:'none'}}
                 >
                  {"Don't have an account? Sign Up"}
                </Typography>
              </Grid>
            </Grid>
          </Box>
        </Box>
        
      </Container>
    
  );
}