import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { Paper } from '@mui/material';
import { Link } from 'react-router-dom';
import { FieldValues, useForm } from 'react-hook-form';
import { LoadingButton } from '@mui/lab';
import agent from '../../app/api/agent';
import { useAppDispatch } from '../../app/store/configureStore';
import { toast } from 'react-toastify';
import { SignInUser } from './accountSlice';
import { history } from '../..';


export default function Register() {
  const dispatch = useAppDispatch();
  const {register ,handleSubmit, setError,formState:{isSubmitting , errors, isValid}}= useForm({
    mode:'all'
  });

 const handelApiErrors=(errors:string[])=>{
  if(errors){
    errors.forEach(error=>{
      if(error.includes('Password')){
        setError('password',{message:error});
      }
      if(error.includes('Email')){
        setError('email',{message:error});
      }
      if(error.includes('Username')){
        setError('username',{message:error});
      }
    })
  }
 }
  return (
    
      <Container component={Paper} maxWidth="sm">     
        <Box
          sx={{
            marginTop: 13,
            p:3,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'primary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Register
          </Typography>
          <Box component="form" onSubmit={handleSubmit((data:FieldValues)=>
          agent.Account.register(data)
          .then(async()=>{
            toast.success('Registeration Successful');
            await dispatch(SignInUser({Username:data.username, Password:data.password}));
            history.push('../');
          })
          .catch(error=>handelApiErrors(error)))}
           noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              fullWidth
              label="Username"
              autoFocus
              {...register('username',{ required: 'Username is required'})}
              error={!!errors.username}
              helperText={errors?.username?.message as React.ReactNode ?? ""}
                
            />
             <TextField
              margin="normal"
              fullWidth
              label="Email"
              {...register('email',{
                 required: 'email is required',
                 pattern:{
                  value:/^\w+[\w-.]*@\w+((-\w+)|(\w*))\.[a-z]{2,3}$/,
                  message:'Not a vaild email address'
                 }
                })}
              error={!!errors.email}
              helperText={errors?.email?.message as React.ReactNode ?? ""}
                
            />
            <TextField
              margin="normal"
              fullWidth
              label="Password"
              type="password"
              {...register('password',{
                 required: 'Password is required',
                 pattern :{
                  value:/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*\W)(?!.*\s).{8,}$/,
                  message : 'Password does not meet complexity requirements'
                 }
                })}
              error={!!errors.password}
              helperText={errors?.password?.message as React.ReactNode ?? ""}
            />
       
            <LoadingButton
            disabled={!isValid}
            loading={isSubmitting}
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Ragister
            </LoadingButton>
            <Grid container>
             
              <Grid item>
                <Typography 
                component={Link}
                 to="../login" 
                 sx={{fontWeight:'bold' , color:'primary.main' , textDecoration:'none'}}
                 >
                  {"Already have an account? Sign In"}
                </Typography>
              </Grid>
            </Grid>
          </Box>
        </Box>
        
      </Container>
    
  );
}
