import ProductList from "./ProductList";
import Loader from "../../app/layout/Loader";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { setPageNumber, setProductParams,} from "./catalogSlice";
import { Grid, Paper,} from "@mui/material";
import ProductSearch from "./ProductSearch";
import RadioButtonGroup from "../../app/components/RadioButtonGroup";
import CheckBoxButtons from "../../app/components/CheckBoxButtons";
import AppPagination from "../../app/components/AppPagination";
import useProducts from "../../app/hooks/useProducts";

const sortOptions = [
  { value: "name", label: "Alphabetical" },
  { value: "lowprice", label: "Price - Low To High" },
  { value: "highprice", label: "Price - High To Low" },
];
export default function Catalog() {
  const { products,filtersLoaded,brands,types,paginationData}=useProducts();
  const dispatch = useAppDispatch();
 const {productParams} =useAppSelector(state=>state.catalog);
  if (! filtersLoaded) 
    return <Loader message="Loading Products ..." />;
  return (
    <Grid container spacing={2}>
      <Grid sx={{ mt: 3 }} item xs={6} sm={2.2}>
        <Paper sx={{ mb: 2 }}>
          <ProductSearch/>
        </Paper>
        <Paper sx={{ mb: 2, p: 2 }}>
          <RadioButtonGroup 
          selectedValue={productParams.orderBy}
          options={sortOptions}
          onChange={(e)=>dispatch(setProductParams({orderBy:e.target.value}))}
          />
        </Paper>
        <Paper sx={{ mb: 2, p: 2 }}>
         <CheckBoxButtons
         items={types}
         checked={productParams.types}
         onChange={(items:string[]) =>dispatch(setProductParams({types:items}))}
         />
        </Paper>
        <Paper sx={{ mb: 2, p: 2 }}>
        <CheckBoxButtons
         items={brands}
         checked={productParams.brands}
         onChange={(items:string[]) =>dispatch(setProductParams({brands:items}))}
         />
        </Paper>
      </Grid>
      <Grid item xs={6} sm={9.8}>
        <ProductList products={products} />
      </Grid>
      <Grid item xs={6} sm={2.2} />
      <Grid item xs={6} sm={9.8}>
        {paginationData && <AppPagination 
            paginationData={paginationData}
            onPageChange={(page:number)=>dispatch(setPageNumber({pageNumber:page}))}
          />}
          
      </Grid>
    </Grid>
  );
}
