import { Box, Paper, Typography, Grid, Button } from "@mui/material";
import { FieldValues, useForm, FormProvider } from "react-hook-form";
import AppTextInput from "../../app/components/AppTextInput";
import { Product } from "../../app/models/product";
import { useEffect, useState } from "react";
import useProducts from "../../app/hooks/useProducts";
import AppSelectList from "../../app/components/AppSelectList";
import AppDropZone from "../../app/components/AppDropZone";
import * as yup from 'yup';
import { useAppDispatch } from "../../app/store/configureStore";
import agent from "../../app/api/agent";
import { setProduct } from "../catalog/catalogSlice";
import { LoadingButton } from "@mui/lab";
type Props = {
  product?: Product,
  cancelEdit: () => void
}

interface ValidationErrors {
  [key: string]: string;
}

export default function ProductForm({ product, cancelEdit }: Props) {
  
  const schema = yup.object().shape({
    name: yup.string().required('Name is a required field'),
    brand: yup.string().required('Brand is a required field'),
    type: yup.string().required('Type is a required field'),
    price: yup
      .number()
      .typeError('Price must be a valid number')
      .test(
        'is-price-valid',
        'Price must be greater than or equal to 100',
        (value) => value !== undefined && value >= 100
      )
      .required('Price is required'),
    quantityInStock: yup
      .number()
      .typeError('Quantity must be a valid number') // Custom error message for invalid number
      .required('Quantity is required') // Make the field required
      .min(0, 'Quantity must be greater than or equal to 0'),
    description: yup.string().required('Description is a required field'),
    file: yup
  .mixed()
  .test('isRequired', 'Please upload an image', function (value) {
    const pictureUrl = this.parent.pictureUrl;

    // Validate based on the condition
    if (!value && !pictureUrl) {
      return false;
    }

    return true;
  })
  .nullable(),

     
});

  

  const methods = useForm({
    
  });

  const { control, handleSubmit, setError, reset, watch ,formState:{isDirty,isSubmitting}} = methods;
  const watchFile = watch( 'file',  null );
  const dispatch=useAppDispatch();
  const [validationErrors] = useState<ValidationErrors>({});
  const { brands, types } = useProducts();
  
  useEffect(() => {
    if (product && !watchFile && !isDirty) reset(product)
    return()=>{
  if(watchFile) URL.revokeObjectURL(watchFile.preview);
}
  }, [product, reset ,watchFile ,isDirty]);

 
  const handelSubmitData = async (data: FieldValues) => {
    try {
      await schema.validate(data, { abortEarly: false });
  
      const isFileSelected = !!watchFile; // Check if a file is selected

      if (!data.pictureUrl && !isFileSelected) {
        throw new Error('Please upload an image');
      }
  
      let response :Product;
      if(product){
        response = await agent.Admin.updateProduct(data);
        
      }else{
        response = await agent.Admin.createProduct(data);
      }
      dispatch(setProduct(response));
        cancelEdit();
      console.log('Data submitted:', data);
      // Perform other validations here
      
      console.log('Validation successful');
      // Clear all errors when the form is valid
      Object.keys(validationErrors).forEach((fieldName) => {
        setError(fieldName, { type: 'manual', message: '' });
      });
    } catch (validationErrors) {
      if (validationErrors instanceof yup.ValidationError) {
        validationErrors.inner.forEach((error) => {
          const path = error.path || 'unknown';
          setError(path, { type: 'manual', message: error.message });
          console.log(`Validation error in field '${path}': ${error.message}`);
         
        });
        
      }
     
    }
  };
  


      
    return (
      
      <Box component={Paper} sx={{ px: 4, py: 2, mt: 10, mx: 2 }}>
      <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
        Product Details
      </Typography>
      <FormProvider {...methods}>
        <form onSubmit={handleSubmit(handelSubmitData)}>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={12}>
                    <AppTextInput   name="name" control={control} label='Product name' />
                    {validationErrors['name'] && (
                     <span style={{ color: 'red' }}>{validationErrors['name']}</span>)}
                </Grid>
                <Grid item xs={12} sm={6}>
                    <AppSelectList control={control} items={brands} name='brand' label='Brand' />
                    {validationErrors['brand'] && (
                     <span style={{ color: 'red' }}>{validationErrors['brand']}</span>)}
                </Grid>
                <Grid item xs={12} sm={6}>
                    <AppSelectList control={control} items={types} name='type' label='Type' />
                    {validationErrors['type'] && (
                     <span   style={{ color: 'red' }}>{validationErrors['type']}</span>)}
                </Grid>
                <Grid item xs={12} sm={6}>
                    <AppTextInput type="number" control={control} name='price' label='Price' />
                    {validationErrors['price'] && (
                    <span style={{ color: 'red' }}>{validationErrors['price']}</span>)}
                </Grid>
                <Grid item xs={12} sm={6}>
                    
                    <AppTextInput type="number" control={control} name='quantityInStock' label='Quantity in Stock' />
                    {validationErrors['quantityInStock'] && (
                    <span style={{ color: 'red' }}>{validationErrors['quantityInStock']}</span>)}
                </Grid>
                <Grid item xs={12}>
                    <AppTextInput multiline rows={3} control={control} name='description' label='Description' />
                    {validationErrors['description'] && (
                    <span style={{ color: 'red' }}>{validationErrors['description']}</span>)}
                </Grid>
                <Grid item xs={12}>
                    <Box display='flex'>
                    <AppDropZone control={control} name='file' />
                   
                    {
                        watchFile ?(
                            <img src={watchFile.preview} alt="preview" style={{marginRight:80, maxHeight:150}}/>
                        ):(
                            <img src={product?.pictureUrl} alt={product?.name}style={{marginRight:80, maxHeight:150}}/>
                        )
                    }
                     
                    </Box>
                  
                </Grid>
            </Grid>
            <Box display='flex' justifyContent='space-between' sx={{ mt: 3 }}>
                <Button onClick={cancelEdit} variant='contained' color='inherit'>Cancel</Button>
                <LoadingButton loading={isSubmitting} type="submit" variant='contained' color='primary'>Submit</LoadingButton>
            </Box>
            </form>
            </FormProvider>
        </Box>
        
    );
}