import { Remove, Add, Delete } from "@mui/icons-material";
import { LoadingButton } from "@mui/lab";
import { TableContainer, Paper, Table, TableHead, TableRow, TableCell, TableBody, Box } from "@mui/material";
import { Link } from "react-router-dom";
import { currencyFormat } from "../../app/helper/helper";
import { removeBasketItemAsync, addBasketItemAsync } from "./basketSlice";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { BasketItem } from "../../app/models/basket";

type Props = {
items :BasketItem[]
isBasket? :boolean
}
export default function BasketTable({items,isBasket=true}:Props) {
    const {status } = useAppSelector(state=>state.basket);
  const dispatch = useAppDispatch();
  return (
    <TableContainer sx={{mt:10}} component={Paper}>
      <Table sx={{ minWidth: 650 }} >
        <TableHead>
          <TableRow>
            <TableCell>Product</TableCell>
            <TableCell align="center">Price</TableCell>
            <TableCell align="center">Quantity</TableCell>
            <TableCell align="center">Subtotal</TableCell>
            {isBasket &&
            <TableCell align="center">Delete</TableCell>
            }
            
          </TableRow>
        </TableHead>
        <TableBody>
          {items.map(item => (
            <TableRow
              key={item.productId}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                <Box 
                component={Link}
                to={`../catalog/${item.productId}`}
                sx={{color:'#1565c0', textDecoration:'inherit'}}
                display='flex'
                alignItems='center'>
                  <img src={item.pictureUrl} alt={item.name} 
                  style={{height:50, marginRight:20}}/>
                  <span>{item.name}</span>

                </Box>
              </TableCell>
              <TableCell align="center">{currencyFormat(item.price)} </TableCell>
              <TableCell align="center">
                { isBasket && 
                 <LoadingButton 
                 loading={status==='pendingRemoveItem'+item.productId + 'rem'}
                  onClick={()=>dispatch(removeBasketItemAsync({productId:item.productId,quantity:1,name:'rem'}))} color='error'>
                      <Remove/>
                 </LoadingButton>
                  }
                 
                {item.quantity}
                {isBasket && 
                    <LoadingButton
                 loading={status==='pendingAddItem'+item.productId} 
                 onClick={()=>dispatch(addBasketItemAsync({productId:item.productId}))} color='primary'>
                       <Add/>
                  </LoadingButton>
                }
                
                </TableCell>
              <TableCell align="center">{((item.price/100)*item.quantity).toFixed(2)} SK</TableCell>
              
                {isBasket && 
                <TableCell align="center">
                 <LoadingButton 
                 loading={status==='pendingRemoveItem'+item.productId +'del'}
                  onClick={()=>dispatch(removeBasketItemAsync({productId:item.productId,quantity:item.quantity ,name:'del'}))} color='error'>
                     <Delete/>
                   </LoadingButton> 
                   </TableCell>
                }            
                
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}
