
import { Route, Routes } from "react-router-dom";
import Header from "./Header";
import { Box } from "@mui/material";
import Catalog from "../../features/catalog/Catalog";
import ProductDetails from "../../features/catalog/ProductDetails";
import AboutUs from "../../features/about/AboutUs";
import ContactUs from "../../features/contact/ContactUs";
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import ServerError from "../errors/ServerError";
import NotFound from "../errors/NotFound";
import BasketPage from "../../features/basket/BasketPage";
import { useCallback, useEffect, useState } from "react";
import Loader from "./Loader";
import { useAppDispatch } from "../store/configureStore";
import { fetchBasketAsync } from "../../features/basket/basketSlice";
import Register from "../../features/account/Register";
import Login from "../../features/account/Login";
import { fetchCurrentUser } from "../../features/account/accountSlice";
import ProtectedRoute from "./ProtectedRoute";
import Orders from "../../features/orders/Orders";
import CheckoutWrapper from "../../features/checkout/CheckoutWrapper";
import Inventory from "../../features/admin/Inventory";

function App() {
 const dispatch = useAppDispatch();
 const [loading, setLoading] = useState(true);
 const initApp = useCallback(async()=>{
  try {
    await dispatch(fetchCurrentUser());
    await dispatch(fetchBasketAsync());
  } catch (error) {
    console.log(error);
  }
 },[dispatch]);
 useEffect(() => {
  initApp().then(()=>setLoading(false));
 
 }, [initApp])


 if (loading) return <Loader message='Starting Shop...'></Loader>

  return (
    <>
      <ToastContainer theme="colored" position="bottom-right" autoClose={4000} hideProgressBar/>
      <Header/>
       <Box sx={{ mt:7,mb:2, px:2}}>
        <Routes>
          <Route path="/" element={<Catalog/>} />
          <Route path="catalog" element={<Catalog/>} />
          <Route path="catalog/:id" element={<ProductDetails />} />
          <Route path="about" element={<AboutUs/>} />
          <Route path="contact" element={<ContactUs/>} />
          <Route path="basket" element={<BasketPage/>} />
          <Route path="checkout" element={<ProtectedRoute><CheckoutWrapper/></ProtectedRoute>}/>
          <Route path="orders" element={<ProtectedRoute><Orders/></ProtectedRoute>}/>
          <Route path="inventory" element={<ProtectedRoute roles={['Admin']}><Inventory/></ProtectedRoute>}/>
          <Route path="server-error" element={<ServerError/>} />
          <Route path="register" element={<Register/>} />
          <Route path="login" element={<Login/>} />
          <Route path="*" element={<NotFound/>} />


        </Routes>
       </Box>
    </>
  );
}

export default App;
