import Typography from "@mui/material/Typography";
import { Button, ButtonGroup } from "@mui/material";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { decrement, increment } from "./counterSlice";

export default function ContactUs() {
  const dispatch = useAppDispatch();
  const { data, title } = useAppSelector(state => state.counter);
  
  return (
    <>
      <Typography gutterBottom variant="h3" marginTop={10} color="initial">
        Title :{title}
      </Typography>
      <Typography variant="h3" color="initial">
        Data :{data }
      </Typography>
      <ButtonGroup sx={{mt:2}}>
           <Button onClick={()=> dispatch(decrement(1))} variant="contained" color="secondary">DECREMENT</Button>
           <Button onClick={()=> dispatch(increment(1))} variant="contained" color="primary">INCREMENT</Button>
           <Button onClick={()=> dispatch(decrement(5))} variant="contained" color="secondary">DECREMENT 5</Button>
           <Button onClick={()=> dispatch(increment(5))} variant="contained" color="primary">INCREMENT 5</Button>
      </ButtonGroup>
    </>
  );
}
