import { Elements } from "@stripe/react-stripe-js"
import { loadStripe } from "@stripe/stripe-js"
import CheckoutPage from "./CheckoutPage"
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore"
import { useEffect, useState } from "react"
import agent from "../../app/api/agent"
import { setBasket } from "../basket/basketSlice"
import Loader from "../../app/layout/Loader"
import { history } from "../.."

const stripePromise= loadStripe('pk_test_51Nm02NFZ9SrEARXc4lh3MBWoM4lUiDeAjgSuDhOAU1Zs7qmb8yRBI4HnnGjzayn1pdaOOPAPU0oxJ1AoAvIm4otW00zqF77fYd')
export default function CheckoutWrapper() {
  const dispatch = useAppDispatch();
  const {basket} = useAppSelector(state=>state.basket);
  const [loading, setLoading] = useState(true);
  useEffect (()=>{
    if(!basket ||basket.items.length===0){
      history.push('../login');
      return;
    }
     agent.Payment.createPaymentIntent()
     .then(basket=>dispatch(setBasket(basket)))
     .catch(error=>console.log(error))
     .finally(()=>setLoading(false))
     // esint-disable-next-line
  },[dispatch])
  if(loading) return <Loader message="Loading Checkout..."/>
  return (
    <Elements stripe={stripePromise}>
      <CheckoutPage/>
    </Elements>
  )
}
